.categories {
  position: relative;
  &__wrapper {
    display: grid;
    row-gap: 40px;

    @include on-tablet {
      row-gap: 56px;
    }
  }

  &__button {
    padding-block: 14.5px;

    background-color: #292929;
    color: #fff;

    font-weight: 700;
    font-size: 16px;
    line-height: 27px;
    text-align: center;
    border: 2px solid #292929;
    border-radius: 8px;

    @include on-desktop {
      justify-self: left;
      padding-inline: 133px;
    }

    &:hover {
      border: 2px solid #f0f1f4;
      background-color: #f8f8fa;
      color: #292929;
    }

    &:active {
      background-color: #111;
      border: 2px solid #111;
      color: #fff;
    }
  }
}

.category {
  position: relative;
  &__photos {
    @include page-grid;

    margin-bottom: 16px;
  }

  &__link {
    position: relative;

    &--wide {
      @include hover(transform, scale(1.1));

      @include on-tablet {
        grid-column: span 4;
      }

      @include on-desktop {
        grid-column: span 8;
      }
    }

    &--square {
      height: 0;
      padding-bottom: 100%;

      @include hover(transform, scale(1.1));

      @include on-tablet {
        grid-column: span 2;
      }

      @include on-desktop {
        grid-column: span 4;
      }
    }
  }

  &__photo {
    position: absolute;
    width: 100%;
    height: 100%;

    object-fit: cover;
  }

  &__title {
    margin: 0;
    font-weight: 700;
    font-size: 18px;
    line-height: 25.2px;

    @include on-tablet {
      font-size: 24px;
      line-height: 33.6px;
    }
  }
}
